export const CHANGE_NAME = "PROFILE_CHANGE_NAME";
export const SIGN_IN = "PROFILE_SIGN_IN";
export const SIGN_OUT = "PROFILE_SIGN_OUT";


//    , ..      MessageList         (  )
export const changeName = (newName) => ({
    type: CHANGE_NAME,
    payload:
    {
        name: newName,
    }
});

// signIn  signOut    Routes
export const signIn = () => ({
    type: SIGN_IN,
});

export const signOut = () => ({
    type: SIGN_OUT,
});